.home {
    header {
        height: 90px;
        background: $black;
        @include breakpoint(mobile){
            height: 75px;
        }
        .branding {
            @include breakpoint(desktop){
                visibility: hidden;
            }
        }
        ul.menu {
            @include breakpoint(desktop){
                left: 70px;
                > li {
                    padding-left: 25px;
                    padding-right: 0;
                }
            }
        }
    }
    .hero {
        background-color: $black;
        background-position: 114px 59px;
        background-repeat: no-repeat;
        background-size: 91%;
        @include breakpoint(1080p){
            background-size: 58%;
            background-position: 444px 44px;
        }
        @include breakpoint(mobile){
            width: 100%;
            display: block;
            padding-bottom: 60px;
            background-attachment: scroll;
            background-position: -196px -52px;
            background-repeat: no-repeat;
            background-size: 153%;
        }
        background-attachment: fixed;
        color: $white;
        .inner {
            position: relative;
            height: 500px;
            height: 600px; //this is for the banner
            @include breakpoint(mobile){
                height: auto;
            }
            .episode-highlight {
                width: 450px;
                position: relative;
                top: -42px;
                > a img, > img { //logo
                    width: 100%;
                    position: relative;
                    top: 4px;
                    @include breakpoint(mobile){
                        display: none;
                    }
                }
                @include breakpoint(mobile){
                    width: 100%;
                    padding-top: 140px;
                    position: static;
                }
                .episode-details {
                    background: $white;
                    padding: 20px;
                    color: $textcolor;
                    a.ep100 {
                        display: block;
                        width: 100%;
                        text-align: center;
                        color: $brandblue;
                        img {
                            max-width: 100%;
                            margin-bottom: 20px;
                        }
                    }
                    .episode {
                        a {
                            color: $textcolor;
                            text-decoration: none;
                        }
                        a > h4 {
                            margin: 0;
                            width: 70%;
                            font-size: 16px;
                            font-weight: $medium;
                            line-height: 1;
                            color: $textcolor;
                            text-decoration: none;
                        }
                        > h5 {
                            width: 100px;
                            float: right;
                            margin-top: -35px;
                            font-size: 16px;
                            font-weight: $thin;
                            text-align: right;
                            span {
                                color: $brandorange;
                            }
                        }
                    }
                    .powerpress_player {
                        margin: 10px 0;
                    }
                    .image {
                        display: inline-block;
                        width: 125px;
                        vertical-align: top;
                        img {
                            max-width: 120px;
                            margin-right: 10px;
                        }
                        h5 {
                            font-size: 11px;
                            margin: 0;
                            font-weight: $regular;
                            color: $textcolor-liter;
                        }
                    }
                    .content {
                        display: inline-block;
                        width: 281px;
                        @include breakpoint(mobile){
                            width: 190px;
                        }
                        @media (max-width: 320px) {
                            width: 280px;
                        }
                        h3 {
                            font-size: 12px;
                            margin: 0;
                        }
                        p {
                            font-size: 16px;
                            font-weight: $light;
                            padding: 0;
                            margin: 0;
                            margin-bottom: 10px;
                            @include breakpoint(mobile){
                                font-size: 14px;
                            }
                        }
                        .tag-cloud {
                            padding-top: 0;
                            li {
                                margin-right: 5px;
                                display: none;
                                line-height: 1.15em;
                                a {
                                    padding-top: 0.2em;
                                    color: $white;
                                    font-size: 10px;
                                    white-space: nowrap;
                                    @include breakpoint(mobile){
                                        white-space: nowrap;
                                    }
                                }
                                &.more {
                                    display: inline !important;
                                    font-size: 11px;
                                }
                            }
                        }
                    }
                }
            }
        }
        .newsletter-widget {
            position: absolute;
            right: 0;
            width: 100%;
            bottom: 100px;
            transition: all 500ms;
            @include breakpoint(mobile){
                display: none;
            }
            .newsletter-form {
                position: relative;
                height: 1px;
                overflow: hidden;
                background: rgba(255,255,255,0.9);
                background: $brandorange2;
                color: $white;
                //color: $textcolor;
                padding: 10px 15px;
                border-top-left-radius: 2px;
                border-top-right-radius: 2px;
                opacity: 0;
                transition: all 500ms;
                .close-trigger {
                    position: absolute;
                    top: 10px;
                    right: 10px;
                    a {
                        background: $brandblue;
                        display: block;
                        width: 20px;
                        height: 20px;
                        text-align: center;
                        line-height: 20px;
                        color: $white;
                        border-radius: 2px;
                        font-size: 28px;
                        padding: 1px 0 0 1px;
                    }
                }
            }
            a {
                display: block;
                padding: 0.5em 6em;
                text-align: center;
                background: rgba(255,255,255,0.9);
                background: $brandorange2;
                border-radius: 2px;
                color: $white;
                text-decoration: none;
                font-size: 26px;
                line-height: 1.3em;
                strong {
                    font-weight: $medium;
                    text-decoration: underline;
                }
            }
            .buttonText-default, .buttonText-cancel {
                display: none;
            }
            &.active {
                .newsletter-form {
                    display: block;
                    height: 155px;
                    opacity: 1;
                }
                a {
                    border-top-right-radius: 0;
                    border-top-left-radius: 0;
                }
            }
            .mc4wp-form.mc4wp-form-421 {
                .mc4wp-form-fields {
                    margin-top: 15px;
                    div.form-row {
                      display: flex;
                      flex-flow: row;
                      justify-content: space-between;
                      p {
                          width: 49%;
                          margin: 0;
                          padding: 0;
                          font-family: "Roboto", sans-serif;
                          label {
                              display: block;
                              font-size: 10px;
                              &.sr-only {
                                  display: none;
                              }
                          }
                          input {
                              display: block;
                              font-size: 16px;
                              width: 100%;
                              padding: 5px;
                              margin-bottom: 8px;
                          }
                        }
                    }
                    p.submit {
                      margin: 0;
                    }
                    input[type="submit"] {
                        margin-top: 7px;
                        background: $brandblue;
                        color: $white;
                        font-weight: bold;
                        border: solid 1px darken($brandgreen, 20%);
                        padding: 8px 0;
                        font-size: 15px;
                        width: 100%;
                        font-weight: $thin;
                        text-transform: uppercase;
                        font-family: "Roboto", sans-serif;
                    }
                }
            }
        }
    }
    .episodes {
        background: linear-gradient(#f6f5d9, #aaa79f);
        padding: 40px 0;
        @include breakpoint(mobile){
            padding-bottom: 0;
        }
        h2 {
            font-size: 24px;
            margin-top: -115px;
            color: $white;
            font-weight: $thin;
            @include breakpoint(mobile){
                margin-top: -80px;
                text-align: center;
            }
        }
        .item {
            background: $white;
            overflow: hidden;
            .inner {
                padding: 15px;
                position: relative;
                a {
                    text-decoration: none;
                    h4 {
                        height: 3em;
                        margin: 0;
                        font-size: 12px;
                        line-height: 1.1;
                        text-decoration: none;
                        color: $textcolor;
                    }
                }
                .image {
                    max-height: 270px;
                    overflow: hidden;
                    h5 {
                        display: inline-block;
                        padding: 3px;
                        padding-bottom: 1px;
                        margin: 0;
                        text-align: right;
                        color: $textcolor-liter;
                        background: $white;
                        position: absolute;
                        top: 58px;
                        right: 25px;
                        z-index: 10;
                        line-height: 10px;
                        border-radius: 2px;
                        font-weight: $light;
                        font-size: 12px;
                        span {
                            color: $brandorange;
                        }
                    }
                    p {
                        position: absolute;
                        bottom: 0;
                        height: 70px;
                        padding: 10px;
                        width: calc(100% - 30px) ;
                        line-height: 1.2;
                        background: $black90;
                        color: $white;
                        font-size: 11px;
                    }
                    img {
                        width: 100%;
                    }
                }
            }
        }
        .episode-carousel {
            .owl-nav {
                position: absolute;
                top: 40%;
                width: 100%;
                @include breakpoint(mobile){
                    display: none;
                }
                button {
                    font-size: 300px;
                    font-weight: $bold;
                    color: $white;
                    position: absolute;
                    text-shadow: 0px 0px 5px $black50;
                    color: #f6f5d9;
                    opacity: 0.6;
                    transition: all 500ms;
                    //color: #aaa79f;
                    &.owl-prev {
                        left: -80px;
                    }
                    &.owl-next {
                        right: -80px;
                    }
                    &:hover {
                        opacity: 1;
                    }
                    &:active, &:focus {
                        outline: none;
                    }
                }
            }
        }
    }
    .about {
        background: linear-gradient(#b1e2e2, #94bebe);
        padding: 40px 0;
        position: relative;
        h2 {
            width: 780px;
            margin: 0 auto;
            font-weight: $regular;
            margin-bottom: 15px;
            color: $brandblue;
            em {
                font-style: normal;
                font-weight: $thin;
            }
            @include breakpoint(mobile){
                width: 100%;
                padding: 0 15px;
                margin-bottom: 0;
                text-align: center;
            }
        }
        .inner {
            background: $white;
            width: 780px;
            margin: 0 auto;
            padding: 20px;
            position: relative;
            @include breakpoint(mobile){
                width: 100%;
                background: none;
            }
            .content {
                overflow: hidden;
                @include breakpoint(mobile){
                    background: $white;
                    padding: 20px;
                }
                img {
                    float: left;
                    width: 225px;
                    @include breakpoint(mobile){
                        display: none;
                    }
                }
                p {
                    float: left;
                    font-size: 16px;
                    font-weight: $thin;
                    line-height: 1.7;
                    width: 434px;
                    padding: 0;
                    margin: 0;
                    margin-left: 20px;
                    padding-bottom: 15px;
                    &:nth-child(4){
                        width: 100%;
                        margin-left: 0;
                    }
                    @include breakpoint(mobile){
                        width: 100%;
                        margin-left: 0;
                        font-weight: $regular;
                    }
                    strong {
                        font-weight: $regular;
                    }
                    &.crosspromo {
                      background: $black10;
                      background: #f6d7a0;
                      display: block;
                      width: 100%;
                      margin-left: 0;
                      padding: 1em 2em;
                      font-weight: 400;
                      a {
                        color: $brandorange;
                        font-weight: 600;
                        &:hover {
                          color: $brandblue;
                        }
                      }
                    }
                }
                &:after {
                    clear: both;
                }
            }
            .flourish {
                width: 166px;
                position: absolute;
                top: -28px;
                right: -80px;
                @include breakpoint(mobile){
                    width: 61px;
                    top: -64px;
                    right: 0;
                }
                img {
                    width: 100%;
                }
            }
        }
    }
    .reviews {
        background: $brandgreen;
        padding: 40px 0;
        color: $white;
        .review-container {
            // display: grid;
            // grid-template-columns: 1fr 1fr;
            // grid-template-rows: 1fr;
            // grid-template-areas: ". .";
            // grid-gap: 30px;
            overflow: hidden;
            @include breakpoint(mobile){
                grid-template-columns: 1fr;
                grid-template-rows: 1fr;
                grid-template-areas: ".";
            }
            .review {
                @include breakpoint(mobile){
                    padding: 0 40px;
                }
                img {
                    display: block;
                    width: 200px;
                    margin: 0 auto;
                    margin-bottom: 15px;
                }
                h3 {
                    font-size: 16px;
                    font-weight: $bold;
                    text-align: center;
                    margin: 0;
                    padding: 0;
                }
                p {
                    margin-top: 10px;
                    font-size: 16px;
                    font-weight: $thin;
                    line-height: 1.8;
                    text-align: justify;
                }
                h5 {
                  font-weight: normal;
                  font-style: italic;
                  margin-top: 0;
                }
            }
        }
    }
    .grooveletters {
        padding: 25px 0 50px 0;
        @include breakpoint(mobile){
            padding: 15px;
        }
        h2 {
            font-size: 24px;
            text-transform: uppercase;
            font-weight: $regular;
            @include breakpoint(mobile){
                text-align: center;
            }
            span {
                float: right;
                font-size: 16px;
                font-weight: $regular;
                @include breakpoint(mobile){
                    display: none;
                    float: none;
                }
                a {
                    color: $brandblue;
                    &:hover {
                        color: $brandorange;
                    }
                }
            }
        }
        .newsletters {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            grid-template-rows: 1fr;
            grid-template-areas: ". .";
            grid-gap: 30px;
            @include breakpoint(mobile){
                display: block;
            }
            .newsletter {
                @include gradient(darken($tan1, 10%), $tan2);
                border-radius: 4px;
                padding: 15px;
                box-shadow: 0 4px 2px -2px $black25;
                @include breakpoint(mobile){
                    margin-bottom: 15px;
                }
                a {
                    text-decoration: none;
                    color: $white;
                    h3 {
                        margin-top: 0;
                        font-weight: $regular;
                    }
                    p {
                        font-size: 16px;
                        color: $textcolor;
                        font-weight: $light;
                    }
                }
            }
        }
    }
}
