body {
    font-family: "Roboto";
    padding: 0;
    margin: 0;
    line-height: 1.4em;
    color: $textcolor;
    background: linear-gradient(#f6f5d9, #aaa79f);
    &.admin-bar {
        .hamburger{
            top: 68px;
        }
        @include breakpoint(mobile){
            .menu-main-menu-container.active {
                top: 120px;
            }
        }
        .search-trigger {
            top: 70px;
        }
        .search-bar.active {
            margin-top: 29px;
        }
    }
}

h1 {
    font-weight: 100;
}

p.powerpress_links {
    display: none;
}

.single-post,
.single-newsletter,
.single-blogentry,
.page-template-template-artists,
.page-template-template-subpage,
.page-template-template-meetups,
.page-template-template-episodes,
.meetup_events-template-default {
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url('/wp-content/themes/behavioralgrooves/img/bg.png'), linear-gradient(#f6f5d9, #aaa79f);
    background-attachment: fixed;
    @include breakpoint(mobile){
        background-attachment: scroll;
    }
    @include breakpoint(1080p){
        background-size: cover;
    }
    h1 {
        padding: 30px 0 20px 0;
        color: $white;
        font-size: 42px;
        font-weight: $thin;
        text-transform: uppercase;
        @include breakpoint(mobile){
            padding-left: 15px;
        }
    }
    h3 {
        a {
            color: $brandblue;
        }
    }
    p {
        a {
            color: $brandgreen;
        }
    }
}
.page-template-template-episodes {
    a.showmore {
        color: $white;
    }
    .filter-bar {
        background: $white;
        max-width: 960px;
        margin: 15px auto;
        padding: 15px;
        display: grid;
        grid-template-columns: 0.35fr 1fr;
        grid-template-rows: 1fr;
        grid-template-areas: ". .";
        @include breakpoint(mobile){
            display: block;
        }
        .selector {
            vertical-align: top;
            @include breakpoint(mobile){
                width: 100%;
            }
            h2 {
                font-size: 24px;
                font-weight: $regular;
                .filter {
                    color: $brandgreen;
                    display: none;
                    &.active-filter {
                        display: inline;
                    }
                    i {
                        font-style: normal;
                        font-size: 18px;
                        margin-left: 10px;
                        display: none;
                    }
                    &:hover {
                        cursor: pointer;
                    }
                }
            }
        }
        .tag-list {
            h6 {
                margin: 0;
                padding-left: 5px;
            }
            .tag-cloud {
                padding-top: 0;
                a {
                    display: inline-flex;
                    margin: 5px;
                    line-height: 15px;
                    padding-top: 0.25em;
                    &.tag-selected {
                        background: $brandorange;
                        &:before {
                            content: '×';
                            padding-right: 3px;
                            position: relative;
                            top: 3px;
                            font-size: 21px;
                            line-height: 9px;
                        }
                        &:hover {
                            background: $brandorange;
                        }
                    }
                    &.showmore {
                        background: none;
                        color: $textcolor;
                        color: $brandgreen;
                        &:hover {
                            color: $brandorange;
                        }
                        span.less {
                            display: none;
                            padding-left: 0.25em;
                            padding-right: 0.25em;
                        }
                        span.more {
                            padding-left: 0.25em;
                            padding-right: 0.25em;
                        }
                        &.active {
                            color: $brandorange;
                            span.less {
                                display: inline;
                            }
                            span.more {
                                display: none;
                            }
                        }
                    }
                }
                &.secondary-tags {
                    display: none;
                    a {
                        background: $black50;
                        padding-top: 0.2em;
                        line-height: 13px;
                        font-weight: $regular;
                        margin-right: -5px;
                        &:nth-child(-n+10){
                        //display: none !important;
                        //this is handled by <style> tags in template-episodes.php
                        }
                        &:hover {
                            background: $brandgreen;
                        }
                        &:before {
                            top: 2px;
                            font-size: 16px;
                            line-height: 9px;
                        }
                        &.tag-selected {
                            background: $brandorange;
                            &:before {
                                content: '×';
                                padding-right: 3px;
                                position: relative;
                                top: 2px;
                                font-size: 16px;
                                line-height: 9px;
                            }
                            &:hover {
                                background: $brandorange;
                            }
                        }
                    }
                }
            }
        }
    }
    .grid-container-episiodes {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr;
        grid-template-areas: ". . .";
        max-width: 960px;
        grid-gap: 15px;
        margin: 0 auto;
        padding-bottom: 20px;
        @include breakpoint(mobile){
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 1fr;
            grid-template-areas: ". .";
        }
        .episode {
            padding: 15px;
            background: $white;
            font-size: 12px;
            position: relative;
            a {
                text-decoration: none;
            }
            h5 {
                display: inline-block;
                padding: 3px;
                padding-bottom: 1px;
                margin: 0;
                text-align: right;
                color: $textcolor-liter;
                background: $white;
                position: absolute;
                top: 54px;
                right: 25px;
                z-index: 10;
                line-height: 10px;
                border-radius: 2px;
                span {
                    color: $brandorange;
                }
            }
            h4 {
                line-height: 1;
                color: $textcolor;
                text-decoration: none;
                margin-top: 0;
                margin-bottom: 5px;
                height: 2em;
                align-self: baseline;
                @include breakpoint(mobile){
                    height: auto;
                }
            }
            .image {
                position: relative;
                z-index: 8;
                max-width: 100%;
                max-height: 300px;
                padding-bottom: 30px;
                overflow: hidden;
                @include breakpoint(mobile){
                    padding-bottom: 60px;
                }
                img {
                    width: 100%;
                }
            }
            p {
                position: relative;
                z-index: 9;
                position: absolute;
                bottom: 15px;
                width: calc(100% - 30px);
                background: $black;
                color: $white;
                font-size: 12px;
                font-weight: $regular;
                line-height: 1.3em;
                padding: 15px;
                margin-bottom: 0;
                min-height: 59px;
                @include breakpoint(sm){
                    font-size: 10px;
                }
                @include breakpoint(mobile){
                    font-size: 9px;
                    min-height: 66px;
                }
            }
        }
    }
}

.single-post, .single-newsletter, .single-blogentry {
    h1 {
        color: $brandgreen;
        font-weight: $light;
        font-size: 24px;
        padding: 0;
    }
    .post-container {
        margin-top: 25px;
        margin-bottom: 25px;
        @include breakpoint(mobile){
            margin-top: 90px;
            margin-bottom: 0;
        }
    }
    .grid-container {
        display: grid;
        grid-template-columns: 66% 33%;
        grid-template-rows: 1fr;
        grid-template-areas: ". .";
        max-width: 960px;
        margin: 0 auto;
        @include breakpoint(mobile){
            //display: block;
            display: flex;
            flex-flow: column;
        }
    }
    .post-area {
        padding: 15px;
        background: $white;
        @include breakpoint(mobile){
            order: 2;
            width: 100%;
        }
        h1 {
            line-height: 1;
        }
        pre {
            font-size: 10px;
            line-height: 9px;
            margin-top: 0;
        }
        p, li {
        font-size: 16px;
        line-height: 1.9;
        font-weight: 300;
            a {
                color: $brandorange !important;
            }
        }
        li {
            margin-bottom: 15px;
        }
        img {
          max-width: 100%;
          height: auto;
        }
    }

    .side-area {
        @include gradient(#f3f3f3, #F0EDE3);
        @include breakpoint(mobile){
            order: 1;
            width: 100%;
            padding-bottom: 15px;
            h3.title {
                display: block;
                font-size: 24px;
                padding: 15px;
                padding-bottom: 0;
                margin: 0;
                color: $brandgreen;
                font-weight: $light;
                line-height: 1.2;
            }
        }
        h2 {
            font-size: 14px;
            text-align: right;
            background: $brandblue;
            border-bottom: solid 4px $brandgreen;
            color: $white;
            font-weight: $light;
            margin: 0;
            padding: 1.3em 1em 1.3em 0;
            @include breakpoint(mobile){
                text-align: center;
            }
            span {
                color: $brandorange2;
                margin-right: 15px;
                text-transform: uppercase;
            }
        }
        h3 {
            font-size: 12px;
            &.title {
                @include breakpoint(desktop){
                    display: none;
                }
            }
        }

        .extra-info {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 1fr;
            grid-template-areas: ". .";
            .guest {
                padding-left: 15px;
                h3 {
                    font-weight: $thin;
                    font-size: 18px;
                    color: $brandblue;
                }
                .image {
                    img {
                        width: 100%;
                    }
                }
                h5 {
                    background: $brandgreen;
                    color: $white;
                    font-weight: $medium;
                    margin: 0;
                    padding-left: 0.5em;
                    a {
                        color: $white;
                        text-decoration: none;
                    }
                }
            }
            .music {
                padding-right: 15px;
                text-align: right;
                h3 {
                    font-weight: $thin;
                    font-size: 18px;
                    color: $brandblue;
                }
                p {
                    font-size: 14px;
                    line-height: 1;
                    margin-bottom: 15px;
                    padding-left: 10px;
                    a {
                        color: $brandorange;
                    }
                }
            }
        }
        .links {
            padding: 0 15px;
            ul {
                padding-left: 20px;
                li {
                    margin-bottom: 10px;
                }
            }
            p, li {
                font-size: 12px;
                line-height: 1.4;
                font-weight: $medium;
                text-overflow: ellipsis;
                overflow: hidden;
                a {
                    color: $brandorange !important;
                }
            }
        }
        .tags, .transcript, .archives {
            padding: 0 15px;
        }
        .tags {
            .tag-cloud {
                padding-top: 0;
            }
        }
        .transcript {
            padding-bottom: 20px;
            a {
                display: block;
                border-radius: 2px;
                background: $brandgreen;
                color: $white;
                font-weight: $regular;
                text-align: center;
                padding: 0.25em 0;
                text-decoration: none;
                transition: all 500ms;
                font-size: 14px;
                &:hover {
                    background: darken($brandgreen, 15%);
                }
            }
        }
        .archives {
            h3 {
                font-size: 16px;
            }
            li {
                list-style-type: none;
                a {
                    color: $brandorange;
                    &:hover {
                        color: $brandgreen;
                    }
                }
            }
        }
    }
}

.single-post {
    #menu-item-67 {
        a {
            color: $brandgreen;
        }
    }
}

.single-blogentry {
    #menu-item-871 {
        a {
            color: $brandgreen;
        }
    }
}

.single-newsletter, .single-blogentry {
    .post-area {
        table {
            background: $black10;
            padding: 10px;
            width: 100%;
            tr {
                td {
                    width: calc(100% - 200px) !important;
                    font-weight: $light;
                    margin: 0;
                    &:first-child {
                        width: 200px !important;
                        white-space: nowrap;
                        padding-right: 15px;
                        font-weight: $bold;

                    }
                }
            }
        }
    }
    .side-area {
        .archives {
            ul {
                padding: 0;
                li {
                    margin-bottom: 5px;
                    span {
                        display: block;
                        font-size: 10px;
                    }
                    a {
                        color: $brandblue;
                    }
                }
            }
        }
    }
}

.archive.tag {
    h1 {
        color: $brandblue;
        padding: 25px 0;
        @include breakpoint(mobile){
            padding: 0 15px;
            font-size: 24px;
            line-height: 1.3;
        }
        span {
            font-weight: $regular;
            @include breakpoint(mobile){
                display: block;
            }
        }
    }
    .archive-list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr;
        grid-template-areas: ". . .";
        grid-gap: 15px;
        margin-bottom: 30px;
        @include breakpoint(mobile){
            grid-template-columns: 1fr;
            grid-template-areas: ".";
            padding: 15px;
        }
        .archive-item {
            padding: 20px;
            background: $white;
            a {
                text-decoration: none;
                color: $textcolor;
                &:hover {
                    color: $brandorange;
                }
            }
            h2 {
                font-size: 14px;
                color: $textcolor;
                text-decoration: none;
                line-height: 1;
                margin: 0;
            }
            p {
                margin-bottom: 0;
                font-size: 14px;
            }
        }
    }
    .all-tags {
        @include breakpoint(mobile){
            padding: 15px;
        }
        h2 {
            font-weight: $light;
            // color: $white;
        }
        .tag-cloud {
            margin-bottom: 30px;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            grid-template-rows: 1fr;
            grid-template-areas: ". . . .";
            grid-gap: 15px;
            @include breakpoint(mobile){
                grid-template-columns: 1fr 1fr;
                grid-template-rows: 1fr;
                grid-template-areas: ". .";
            }
            a {
                text-align: center;
                background: $black75;
                &:hover {
                    background: $brandorange;
                }
            }
        }
    }
}

.archive.post-type-archive-newsletter {
    .archive {
        background: $white;
        padding: 30px 20px;
        margin: 30px auto;
        h1 {
            margin-top: 0;
            margin-bottom: 30px;
            color: $brandgreen;
            text-transform: uppercase;
        }
        h2 {
            a {
                font-weight: $regular;
                color: $brandblue;
                &:hover {
                    color: $brandorange;
                }
            }
        }
    }
}

.tag-cloud {
    padding: 0;
    margin: 0;
    padding-top: 9px;
    li {
        list-style-image: none;
        list-style-type: none;
        display: inline-block;
        margin-right: 10px;
    }
    a, li > a {
        background: $brandblue;
        color: $white;
        text-decoration: none;
        text-transform: uppercase;
        font-size: 12px;
        font-weight: $bold;
        padding: 0.25em 0.5em 0.1em;
        @include transition(250ms);
        &:hover {
            background: $brandgreen;
        }
    }
}

ul.menu {
    margin: 0;
    padding: 0;
    position: relative;
    top: -7px;
    transition: opacity 250ms;
    &.search-active {
        opacity: 0;
    }
    > li {
        display: inline-block;
        vertical-align: top;
        position: relative;
        padding-left: 0;
        padding-right: 40px;
        @include breakpoint(mobile){
            padding-left: 0;
            padding-right: 0;
            display: block;
            background: $black75;
        }
        a {
            color: $white;
            display: block;
            font-size: 20px;
            font-weight: $thin;
            text-transform: uppercase;
            text-decoration: none;
            @include breakpoint(mobile){
                text-align: center;
                padding: 20px;
                font-weight: $medium;
            }
            &:hover {
                color: $brandgreen;
                @include breakpoint(mobile){
                    background: $black75;
                    color: $brandorange;
                }
            }
        }
        &.menu-item-has-children {
            ul.sub-menu {
                margin: 0;
                padding: 0;
                max-width: 200px;
                position: absolute;
                background: $black75;
                display: none;
                top: 1em;
                @include breakpoint(mobile){
                    //display: block;
                    width: 100%;
                    max-width: none;
                }
                > li {
                    list-style: none;
                    list-style-image: none;
                    text-align: left;
                    a {
                        display: block;
                        padding: 1em;
                        white-space: nowrap;
                        text-transform: none;
                        &:hover {
                            background: $black50;
                        }
                    }
                }
            }
            &:hover {
                a {
                    color: $brandgreen;
                    @include breakpoint(mobile){
                        color: $brandorange;
                    }
                }
                ul.sub-menu {
                    display: block;
                    border-top: solid 2px $brandgreen;
                    margin-top: 8px;
                    z-index: 9999;
                    @include breakpoint(mobile){
                        margin-top: 46px;
                    }
                    li {
                        a {
                            color: $white;
                            padding: 5px 10px;
                            font-size: 14px;
                            font-weight: $light;
                            @include breakpoint(mobile){
                                font-size: 20px;
                                padding: 12px 10px;
                            }
                            @media (max-width: 320px) {
                                font-size: 16px;
                                padding: 8px 10px;
                            }
                            &:hover {
                                color: lighten($brandgreen, 30%);
                            }
                        }
                    }
                }
            }
        }
        &.current-menu-item {
            a {
                color: $brandgreen;
            }
        }
    }
}

.page-template-template-meetups {
    .contained {
        .intro-copy {
            margin-bottom: 30px;
            background: $white;
            p {
                padding: 20px;
                font-size: 16px;
                font-weight: $light;
            }
        }
    }
    .events {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr;
        grid-template-areas: ". .";
        grid-gap: 15px;
        .event {
            padding: 20px;
            background: $white;
            img {
                max-width: 100%;
                display: none;
            }
        }
