    //styles to override plugin base (bootstrap-sorta)
    .row_grid {
        @include breakpoint(mobile){
            margin: 0;
        }
    }
    .ime_archive {
        .img_placeholder {
            display: none;
        }
        .archive-event {
            .ime_event {
                height: 220px;
                .event_details {
                    height: auto;
                    .event_date {
                        background: $brandgreen;
                    }
                    .event_desc {
                        a {
                            text-decoration: none !important;
                            .event_title {
                                font-size: 16px;
                                font-weight: $medium;
                                color: $brandgreen;
                                text-decoration: none !important;
                                text-overflow: inherit;
                                overflow: visible;
                                white-space: normal;
                            }
                        }
                        .event_address {
                            font-size: 14px;
                            text-overflow: inherit;
                            overflow: visible;
                            white-space: normal;
                            padding-left: 30px;
                            margin-top: 20px;
                        }
                    }
                    .fa {
                        color: $black10;
                        float: left;
                        font-size: 42px;
                        padding-bottom: 35px;
                        position: relative;
                        left: -20px;
                    }
                }
            }
        }
    }
}

