        display: inline-block;
        width: 40%;
        vertical-align: top;
        @include breakpoint(mobile){
            width: 100%;
            display: block;
            text-align: center;
        }
        .intro {
            display: inline-block;
            width: 135px;
            text-align: center;
            @include breakpoint(mobile){
