                font-size: 14px;
                margin: 0;
                padding: 0;
                line-height: 14px;
                font-weight: $thin;
            }
            h4 {
                font-size: 20px;
                margin: 0;
                padding: 0;
                line-height: 14px;
                font-weight: $thin;
                @include breakpoint(mobile){
                    margin-bottom: 30px;
                }
            }
        }
        .field {
            display: inline-block;
            width: 235px;
            text-align: right;
            padding-left: 10px;
            input {
                width: 95%;
                border: none;
                padding: 1em 0.5em;
                position: relative;
                top: -7px;
            }
        }
        .mc4wp-form.mc4wp-form-421 {
            .mc4wp-form-fields {
                margin-top: 15px;
                p {
                    margin: 0;
                    padding: 0;
                    &.single-line {
                        display: none;
                        &.submit {
                            @include breakpoint(mobile){
                                display: block;
                            }
                        }
                    }
                    label {
                        display: block;
                        font-size: 10px;
                        &.sr-only {
                            display: none;
                        }
                    }
                    input {
                        display: block;
                        font-size: 14px;
                        width: 100%;
                        padding: 5px;
                        margin-bottom: 4px;
                    }
                    input[type="submit"]{
                        margin-top: 7px;
                        background: $brandgreen;
                        color: $white;
                        border: solid 1px darken($brandgreen, 30%);
                        padding: 8px 0;
                        font-size: 15px;
                    }
                }
            }
            .mc4wp-response {
                position: absolute;
                color: $brandorange;
            }
        }
    }
    .menu-main-menu-container {
        display: inline-block;
        width: 59%;
        text-align: right;
        @include breakpoint(mobile){
            display: none;
        }
        ul.menu {
            margin: 0;
            padding: 0;
            top: 12px;
            > li {
                vertical-align: top;
                padding-left: 25px;
                padding-right: 0;
                a {
                    color: $white;
                    &:hover {
                        color: $brandorange2;
                    }
                }
                &.menu-item-has-children {
                    &:hover {
                        a {
                            color: $brandorange2;
                        }
                    }
                    ul.sub-menu {
                        margin: 0;
                        padding: 0;
                        max-width: 150px;
                        position: absolute;
                        top: -190px;
                        border-top: none;
                        > li {
                            list-style: none;
                            list-style-image: none;
                            text-align: left;
                            &:hover {
                                color: $brandorange2;
                            }
                            a {
                                &:hover {
                                    color: $brandorange2;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .copyright {
        width: 100%;
        text-align: center;
        padding: 50px 0;
        padding-bottom: 0;
        font-size: 12px;
        font-weight: $regular;
        opacity: 0.4;
    }
}

.search-trigger {
    position: absolute;
    top: 43px;
    right: 24px;
    width: 28px;
    height: 38px;
    @include breakpoint(mobile){
        display: none;
    }
    a {
        display: block;
        img {
            width: 100%;
        }
    }
}

.search-bar {
    height: 110px;
    width: 960px;
    margin: 0 auto;
    text-align: right;
    position: absolute;
    top: 0;
    margin-top: -250px;
    transition: all 500ms;
    .searchform {
        margin-bottom: 0;
        line-height: 120px;
        label {
            font-size: 24px;
            color: $white;
        }
        input[type="text"]{
            font-size: 24px;
            border: solid 1px $brandgreen;
            background: $black;
            color: $white;
            padding: 0.25em 1em;
            &:active, &:focus {
                outline: none;
            }
        }
        input[type="submit"]{
            font-size: 18px;
            background: $brandgreen;
            color: $white;
            border: solid 1px $brandgreen;
            padding: 9px 16px 10px 16px;
            position: relative;
            top: -3px;
            left: -4px;
        }
    }
    &.active {
        margin-top: 0;
    }
}

.search-results {
    h1 {
        em {
            font-style: normal;
            font-weight: $medium;
            color: $brandblue;
        }
    }
    .content-area {
        article {
            border-top: solid 1px $black25;
            &:first-child {
                border-top: none;
            }
            h2 {
                font-weight: $regular;
                font-size: 18px;
                a {
                    color: $brandgreen;
                    text-decoration: none;
                    &:hover {
                        color: $brandblue;
                        text-decoration: underline;
                    }
                }
            }
        }
    }
}

.hamburger {
    display: none;
    @include breakpoint(mobile){
        display: block;
    }
    position: absolute;
    top: 25px;
    right: 25px;
    span {
        display: block;
        height: 2px;
        width: 30px;
        background: $white;
        margin-bottom: 10px;
        transition: all 250ms;
        position: relative;
    }
    &.active {
        span.top {
            transform: rotate(45deg);
            top: 12px;
        }
        span.mid {
            width: 0;
            left: 15px;
        }
        span.bot {
            transform: rotate(-45deg);
            top: -12px;
        }
    }
}

.page-template-template-artists {
    .grid-container-artists {
        background: $white;
        padding: 20px;
        margin: 30px auto;
    }
}

.subpage {
    h1.contained {
        padding: 30px 0;
        text-transform: uppercase;
        @include breakpoint(mobile){
            padding-left: 15px;
        }
    }
    .content-area {
        background: $white;
        padding: 20px;
        margin-bottom: 30px;
    }
}

.wpcf7 {
    input[type="text"],
    input[type="email"],
    textarea {
        padding: 10px;
        font-size: 16px;
        border: solid 1px $black25;
        @include breakpoint(mobile){
            max-width: 100%;
        }
    }
    input[type="submit"]{
        background: $brandgreen;
        color: $white;
        display: block;
        font-size: 16px;
        padding: 0.5em 2em;
        @include breakpoint(mobile){
            width: 100%;
            padding: 2em;
        }
    }
}

.page-template-template-newsletters, .page-template-template-blogentries {
    .grid-container-newsletters {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr;
        grid-template-areas: ". . .";
        max-width: 960px;
        grid-gap: 15px;
        margin: 0 auto;
        padding-bottom: 20px;
        @include breakpoint(mobile){
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 1fr;
            grid-template-areas: ". .";
        }
        .newsletter, .blogentry {
            a {
                text-decoration: none;
                color: $textcolor;
                h4 {
                    font-size: 10px;
                    margin: 0;
                }
                h3 {
                    font-size: 16px;
                    margin: 0;
                }
                p {
                    font-size: 12px;
                }
            }
        }
    }
}
